// Section - Services //

.services {
  padding: 60px 0;
}

.section-title p {
  padding-bottom: 25px;
}

.card {
  border-radius: 20px;
  border: none;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
  transition: all 0.5s ease-in-out;
  margin: 0 15px;
  max-height: 305px;
}

.col {
  margin: 25px 0;
}

.card-img {
  border-radius: 20px 20px 0 0;
  height: 220px;
  transition: all 0.7s ease-in-out;
  background: linear-gradient(45deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 100%),
    url("../img/services/services-1.jpeg") center (center / cover) no-repeat;
}

.card-img-2 {
  background: linear-gradient(45deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 100%),
    url("../img/services/services-2.jpeg") (center / cover) no-repeat;
}

.card-img-3 {
  background: linear-gradient(45deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 100%),
    url("../img/services/services-3.jpeg") (center / cover) no-repeat;
}

.card-img-4 {
  background: linear-gradient(45deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 100%),
    url("../img/services/services-4.jpeg") (center / cover) no-repeat;
}

.card-img-5 {
  background: linear-gradient(45deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 100%),
    url("../img/services/services-5.jpeg") (top / cover) no-repeat;
}

.card-img-6 {
  background: linear-gradient(45deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 100%),
    url("../img/services/services-6.jpeg") (center / cover) no-repeat;
}

.card-title {
  font-size: 22px;
  font-weight: 700;
}

.card-body {
  padding: 25px;
  background-color: #fff;
  border-radius: 0 0 20px 20px;
  transition: all 0.7s ease-in-out;
}

.card {
  .card-text {
    position: relative;
    opacity: 0;
    height: 0;
    overflow: hidden;
    transition: all 0.7s ease-in-out;
  }

  &:hover {
    // box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.4);
    transition: all 0.5s ease-in-out;

    .card-text {
      opacity: 1;
      height: 100px;
      overflow: scroll;
      transition: all 0.7s ease-in-out;
    }

    .card-body {
      margin-top: -100px;
      transition: all 0.7s ease-in-out;
    }
  }
}

@media (max-width: 1399px) {
  .card:hover {
    .card-text {
      height: 120px;
    }

    .card-body {
      margin-top: -120px;
    }
  }
}

@media (max-width: 1199px) {
  .col {
    width: 50%;
  }

  .card:hover {
    .card-text {
      height: 100px;
    }

    .card-body {
      margin-top: -100px;
    }
  }
}

@media (max-width: 991px) {
  .card:hover {
    .card-text {
      height: 145px;
    }

    .card-body {
      margin-top: -145px;
    }
  }
}

@media (max-width: 767px) {
  .col {
    width: 100%;
  }

  .card:hover {
    .card-text {
      height: 100px;
    }

    .card-body {
      margin-top: -100px;
    }
  }
}

@media (max-width: 430px) {
  .section-title h2 {
    font-size: 24px;
  }

  .card:hover {
    .card-text {
      height: 120px;
    }

    .card-body {
      margin-top: -120px;
    }
  }
}

@media (max-width: 370px) {
  .section-title h2 {
    font-size: 24px;
  }

  .card:hover {
    .card-text {
      height: 170px;
    }

    .card-body {
      margin-top: -170px;
    }
  }
}

// Section - Tabs */

.ftco-services .container {
  max-width: 1320px;
  margin: 0 auto;
  padding: 0 30px;
}

.tab-content .py-5 {
  padding: 0rem 1.5rem !important;
}

.ftco-services {
  .nav-link-wrap {
    background: #6355f2;
    border-radius: 20px;
  }

  .nav-pills {
    p {
      margin-bottom: 0;
    }

    .tab-link {
      border-radius: 0;
      color: rgba(255, 255, 255, 0.8);
      font-size: 20px;
      font-weight: 400;
      position: relative;
      transition: all 0.7s ease;
      padding: 10px 12px !important;

      &:after {
        position: absolute;
        top: 20px;
        bottom: 10px;
        right: -12px;
        width: 0;
        height: 0;
        content: "";
        border-style: solid;
        border-width: 10px 10px 10px 0;
        border-color: transparent #fafafa transparent transparent;
        opacity: 0;
      }

      span {
        font-size: 24px;
        margin-right: 5px;
        color: rgba(255, 255, 255, 0.8);
        vertical-align: top;
      }

      &.active,
      &:hover {
        color: #fff;
        font-size: 22px;
        -moz-transition: all 0.3s ease;
        -o-transition: all 0.3s ease;
        -webkit-transition: all 0.3s ease;
        -ms-transition: all 0.3s ease;
        transition: all 0.3s ease;
        background: transparent;
      }

      &.active:after,
      &:hover:after {
        opacity: 1;
      }

      &.active span i,
      &:hover span i {
        color: #fff;
        transition: all 0.3s ease;
      }
    }
  }

  .tab-content {
    .tab-pane .icon {
      font-size: 100px;
      line-height: 1.2;
      color: #5846f9;
    }

    img {
      max-height: 620px;
      max-width: 100%;
      overflow: hidden;
    }
  }
}

@media (min-width: 768px) {
  .p-md-5 {
    padding: 0 1rem !important;
  }

  .tab-pane {
    display: block !important;
    height: 0 !important;
    opacity: 0;
    transform: translateY(50vh);
  }

  .show {
    display: block !important;
    opacity: 1;
    transform: translateY(-300px);
    transition: all 1s ease-in-out;
  }
}

@media (max-width: 767.98px) {
  .ftco-services {
    .nav-link-wrap {
      margin-top: 30px;
    }

    .p-4 {
      padding: 0 !important;
    }
  }

  .tab-content .show {
    margin-top: 90px;
    display: block !important;
  }
}

@media (max-width: 1110px) {
  .ftco-services .nav-pills .tab-link {
    &.active:after,
    &:hover:after {
      opacity: 0;
    }
    display: block;
    width: 100%;
  }
}
